import React from "react";

const ResultPage = (props) => {

  const {
    countError,
    onResetTest,
    correctAnswers,
    totalQuestionsNumber,
    finishTime
  } = props

  return (
    <div className="result-wrapper">
      <h2 className="result-title"> </h2>
      <p>  : {countError}/2</p>
      <p>   : {correctAnswers}/{totalQuestionsNumber} </p>
      <p>  : {finishTime} </p>
      <input
        type="button"
        className="result-button"
        value=" "
        onClick={onResetTest}
      />
    </div>
  )
}

export default ResultPage